<template>
  <div>
    <div class="search">
      <input v-model="keyword" class="search-input" type="text" placeholder="">
    </div>
    <div class="search-content" ref="search" v-show="keyword">
      <ul>
         <li class="search-item border-bottom" v-for="(item, key) of list" :key="key" @click="handleCityClick(item.name)">{{item.name}}</li>
         <li class="search-item border-bottom" v-show="hsaNoData"></li>
      </ul>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
import { mapMutations } from 'vuex'
export default {
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data () {
    return {
      keyword: '',
      list: []
    }
  },
  computed: {
    hsaNoData () {
      return !this.list.length
    }
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      if (!this.keyword) {
        this.list = []
        return
      }
      this.timer = setTimeout(() => {
        const result = []
        for (let i in this.cities) {
          this.cities[i].forEach((value) => {
            if (value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
              result.push(value)
            }
          })
        }
        this.list = result
      }, 100)
    }
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.search, {click: true})
  },
  methods: {
    handleCityClick (city) {
      this.changeCity(city)
      this.$router.push('/')
    },
    ...mapMutations(['changeCity'])
  }
}
</script>

<style lang="stylus" scoped>
    @import '~styles/varibles.styl';
    .search
      height: .70rem
      padding: 0.1rem
      background: $bgColor
      .search-input
        box-sizing: border-box
        width: 100%
        height: .62rem
        padding: 0 .1rem
        line-height: .62rem
        text-align: center
        border-radius: .06rem
        color: #666
    .search-content
      overflow: hidden
      z-index: 1
      position: absolute
      top: 1.7rem
      left: 0
      right: 0
      bottom: 0
      background: #eee
      .search-item
        line-height: .62rem
        padding-left: .2rem
        background: #fff
        color: #666
</style>



